import React, { useState, useEffect } from 'react'
import People from './components/People'
import AddPerson from './components/AddPerson'
import Filter from './components/Filter'
import personService from './services/persons'
import Notification from './components/Notification'
import './App.css'

const App = () => {
  const [persons, setPersons] = useState([])
  const [ newName, setNewName ] = useState('')
  const [ newNumber, setNewNumber] = useState('')
  const [ query, setQuery ] = useState('')
  const [ message, setMessage ] = useState(null)
  const [ messageStyle, setMessageStyle] = useState(null)

  const handleNameChange = (event) => setNewName(event.target.value)
  const handleNumberChange = (event) => setNewNumber(event.target.value)

  const handleSubmit = (event) => {
    event.preventDefault()
    const searchPerson = person => 
      person.name.toLowerCase() === newName.toLowerCase()
    const found = persons.find(searchPerson)
    if(found) {
      const userDecision = window
        .confirm(`${newName} is already added to phonebook. Would you like to edit`)
      if(userDecision){
        personService
          .putPerson(found.id, {name: newName, number: newNumber})
          .then(person => {
            setPersons(persons.map(p => p.id === found.id ? person : p))
            setMessage(`Number of ${person.name} updated`)
            setMessageStyle('message success')
            setTimeout(() => {
              setMessage(null)
              setMessageStyle(null)
            }, 5000)
          })
          .catch(() => {
            setMessage('error already deleted')
            setMessageStyle('message error')
            setTimeout(()=> {
              setMessage(null)
              setMessageStyle(null)
            }, 5000)
            setPersons(persons.filter(person=>person.id !== found.id))
          })
      }
      setNewName('')
      setNewNumber('')
    } else {
        personService
          .create({name:newName, number: newNumber})
          .then(person => {
            setPersons(persons.concat(person))
            setMessage(`Added ${person.name}`)
            setMessageStyle('message success')
            setTimeout(() => {
              setMessage(null)
              setMessageStyle(null)
            }, 5000)
          })
          .catch(error => {
            setMessage(error)
            setMessageStyle('message error')
            setTimeout(() => {
              setMessageStyle(null)
              setMessage(null)
            },5000)
          })
      setNewName('')
      setNewNumber('')
    }
  }

  const handleQueryChange = (event) => {
    setQuery(event.target.value)
  }

  const handleInitialFetch = () => {
    personService
      .getAll()
      .then((persons) => setPersons(persons))
  }

  useEffect(handleInitialFetch, [])

  const handleDeleteClick = (id) => {
    return () => {
      const matchPerson = persons.find(person => person.id === id)
      const isConfirmed = window.confirm(`Would you like to delete ${matchPerson.name}`)
      if(isConfirmed) {
        personService.deletePerson(id)
        setPersons(persons.filter(person => person.id !== id))
      }
    } 
  }

  return (
    <div>
      <h2>Phonebook</h2>
      <Notification message={message} styleMessage={messageStyle}/>
      <div>
        filter shown with 
        <Filter query={query} handleQueryChange={handleQueryChange}/>
      </div>
      <h2>add a new</h2>
      <AddPerson 
        handleSubmit={handleSubmit} 
        handleNumberChange={handleNumberChange}
        handleNameChange={handleNameChange}
        newName={newName}
        newNumber={newNumber}
      />
      <h2>Numbers</h2>
      <div><People persons={persons} query={query} handleDelete={handleDeleteClick}/></div>
    </div>
  )
}

export default App